import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-tongtien',
  templateUrl: './tongtien.component.html',
  styleUrls: ['./tongtien.component.css']
})
export class TongtienComponent implements OnInit {
  constructor() { }

  ngOnInit() {
  }


  s =0;
  tang(id){
    this.dulieu.forEach((item) => {
      if(item.id === id) {
        item.number++;
      }
    })
  }

  giam(id){
    this.dulieu.forEach((item) => {
      if(item.id === id) {
        item.number--;
        if(item.number<0){
          item.number = 0;
        }
      }
    })
  };

  tong(){
    let total = 0;
    this.dulieu.forEach((item) => {
      total += item.price*item.number;
    })

    return Math.ceil(total);
  }
  

  dulieu=[
    {
      id:"01",
      image:"tivi.jpg",
      name:"Tivi",
      price:500.125,
      tang:"+",
      giam:"-",
      view:"view",
      delete:"delete",
      number: 0
    },
    {
      id:"02",
      image:"laptop.jpg",
      name:"Laptop",
      price:600.65464,
      tang:"+",
      giam:"-",
      view:"view",
      delete:"delete",
      number: 0
    }

  ]

}
